.account {
  border-top: 1px solid #203B60;
  padding : 0 24px 0 48px;
  .a-info {
    display : flex;
    flex-direction: column;
    margin: 24px auto 32px auto;  
    .content {

      .trade-warnning  {
        margin-top: 8px;
      }
      .detail-graph {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .detail {  
          .row.title {
            font-size: 16px;
            color: #E0ECFF;
            margin-top: 0;
          }
          .row {
            font-size: 14px;
            margin-top: 16px;
            .value {
              color : #E0ECFF;
              height: 18px;
            }
            .value.v3 {
              position: relative;
              .dyn-eff-bal {
                display : flex;
                align-items: center;
                img {
                  width: 24px;
                }
              }
              .xvs-earned.show {
                visibility: visible;
                z-index: 2;
              }
              .xvs-earned {
                position: absolute;
                top: 24px;
                font-weight: 600;
                font-size: 14px;
                visibility: hidden;
                background-color: #203B60;
                display: flex;
                align-items: center;
                justify-content: space-between;
                border-radius: 4px;
                padding: 4px;    
                display: flex;
                white-space: nowrap;
                .label {
                  margin-right: 8px;
                  white-space: nowrap;
                }
                .claimed {
                  margin-right: 8px;
                }
                .collect {
                  margin-left:8px;
                }
              }
              img {
                cursor: pointer;
              }

            }
          }
        }
      }
      .info-footer {
        display: flex;
        justify-content: space-between;
        margin-top: 16px;
      }
    }
  }
}
@media only screen and (max-width : 760px) {
  .account {
    padding : 0 24px 0 24px;
  }
}