.gauge {
  position: relative;
  .start-point,.point-100,.point-200 ,.point-200-red{
    position: absolute;
    font-size: 14px;
    cursor: pointer;
  }

  .start-point {
    left: 20%;
    top: 78%;
  }
  .point-100 {
    top: 10%;
    left: 80%;
  }
  .point-200 {
    left: 80%;
    top: 78%;
  }
  .point-200-red {
    cursor: initial;
    width: 4px;
    height: 4px;
    left: 326px;
    top: 301px;
    border-radius: 2px;
    background: #CE5E5E;
    left: 73%;
    top: 73%;
  }

}